.icon-container {
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  width: max-content;
}

.icon-row {
  display: flex;
  flex-direction: column;
  gap: 5px;
  justify-content: center;
  align-items: center; 
  padding: 10px;
}

.icon-row span {
  font-size: 10px;
}